.dashboard {
  font-family: 'Arial', sans-serif;
  padding: 20px;
  position: relative;
  min-height: 100vh;
  background-color: #f4f4f4;
}

.dashboard-header {
  display: flex;
  justify-content: flex-end;
  padding: 10px;
}

.logout-button {
  padding: 8px 16px;
  background-color: #d9534f;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
}

.logout-button:hover {
  background-color: #c9302c;
}

.dashboard-main {
  padding: 20px;
  position: relative;
}

.add-password-button {
  position: fixed;
  bottom: 20px;
  right: 20px;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: #007bff;
  color: #fff;
  border: none;
  font-size: 36px;
  box-shadow: 0 4px 8px rgba(0, 123, 255, 0.5);
  cursor: pointer;
}

.add-password-button:hover {
  background-color: #0056b3;
}
